export class ApartmentStatuses {
    constructor () {
        this.ApartmentAPIAddress = 'https://localhost:55683';
    }

    // 1.   
    async GetStatuses() {
        try {
            const response = await fetch(`${this.ApartmentAPIAddress}/api/Status`, {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(` ${response.status}: ${errorText}`);
            }

            const data = await response.json();
            console.log(':', data);
            return data;
        } catch (error) {
            console.error('  :', error.message || error);
        }
    }

    // 2.    ID
    async GetStatus(id) {
        try {
            const response = await fetch(`${this.ApartmentAPIAddress}/api/Status/${id}`, {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                if (response.status === 404) {
                    console.log(`  ID ${id}  .`);
                    return null;
                }
                const errorText = await response.text();
                throw new Error(` ${response.status}: ${errorText}`);
            }

            const data = await response.json();
            console.log(` ${id}:`, data);
            return data;
        } catch (error) {
            console.error(`   ${id}:`, error.message || error);
        }
    }

    // 3.   
    async CreateStatus(name) {
        try {
            const response = await fetch(`${this.ApartmentAPIAddress}/api/Status`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ name: name })
            });

            if (!response.ok) {
                let errorMessage;
                const contentType = response.headers.get("content-type");
                if (contentType && contentType.includes("application/json")) {
                    const errorData = await response.json();
                    errorMessage = ` ${response.status}: ${JSON.stringify(errorData)}`;
                } else {
                    const errorText = await response.text();
                    errorMessage = ` ${response.status}: ${errorText}`;
                }
                throw new Error(errorMessage);
            }

            const data = await response.json();
            console.log(' :', data);
            return data;
        } catch (error) {
            console.error('  :', error.message || error);
        }
    }

    // 4.  
    async UpdateStatus(id, newName) {
    try {
        const response = await fetch(`${this.ApartmentAPIAddress}/api/Status/${id}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ name: newName }) // ,  DTO UpdateStatusDto   name
        });

        if (!response.ok) {
            if (response.status === 404) {
                console.log(`  ID ${id}    .`);
                return false;
            }
            let errorMessage;
            const contentType = response.headers.get("content-type");
            if (contentType && contentType.includes("application/json")) {
                const errorData = await response.json();
                errorMessage = ` ${response.status}: ${JSON.stringify(errorData)}`;
            } else {
                const errorText = await response.text();
                errorMessage = ` ${response.status}: ${errorText}`;
            }
            throw new Error(errorMessage);
        }

        console.log(` ${id} .`);
        return true;
    } catch (error) {
        console.error(`   ${id}:`, error.message || error);
        return false; //  false   
    }
    }

    // 5.  
    async DeleteStatus(id) {
    try {
        const response = await fetch(`${this.ApartmentAPIAddress}/api/Status/${id}`, {
            method: 'DELETE',
            headers: { 'Content-Type': 'application/json' }
        });

        if (!response.ok) {
            if (response.status === 404) {
                console.log(`  ID ${id}    .`);
                return false;
            }
            // ,    409 Conflict (  )
            if (response.status === 409) {
                const errorText = await response.text();
                console.log(`    ${id}: ${errorText}`);
                return false;
            }
            const errorText = await response.text();
            throw new Error(` ${response.status}: ${errorText}`);
        }

        console.log(` ${id} .`);
        return true;
    } catch (error) {
        console.error(`   ${id}:`, error.message || error);
        return false; //  false   
    }
    }

    // 6.     
    async AssignStatusToUser(apartmentId, userId, statusId) {
    try {
        const response = await fetch(`${this.ApartmentAPIAddress}/api/Status/apartment/${apartmentId}/user/${userId}/status/${statusId}`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' }
        //       
        });

        if (!response.ok) {
        let errorMessage;
        const contentType = response.headers.get("content-type");
        if (contentType && contentType.includes("application/json")) {
            const errorData = await response.json();
            errorMessage = ` ${response.status}: ${JSON.stringify(errorData)}`;
        } else {
            const errorText = await response.text();
            errorMessage = ` ${response.status}: ${errorText}`;
        }
        throw new Error(errorMessage);
        }

        const successMessage = await response.text(); //   
        console.log(successMessage);
        return true;
    } catch (error) {
        console.error('   :', error.message || error);
        return false; //  false   
    }
    }

    // 7.      
    async RevokeStatusFromUser(apartmentId, userId, statusId) {
        try {
            const response = await fetch(`${this.ApartmentAPIAddress}/api/Status/apartment/${apartmentId}/user/${userId}/status/${statusId}`, {
                method: 'DELETE',
                headers: { 'Content-Type': 'application/json' }
                //       
            });

            if (!response.ok) {
                if (response.status === 404) {
                    console.log(`  ${statusId}   ${userId}   ${apartmentId}  .`);
                    return false;
                }
                let errorMessage;
                const contentType = response.headers.get("content-type");
                if (contentType && contentType.includes("application/json")) {
                    const errorData = await response.json();
                    errorMessage = ` ${response.status}: ${JSON.stringify(errorData)}`;
                } else {
                    const errorText = await response.text();
                    errorMessage = ` ${response.status}: ${errorText}`;
                }
                throw new Error(errorMessage);
            }

            const successMessage = await response.text(); //   
            console.log(successMessage);
            return true;
        } catch (error) {
            console.error('    :', error.message || error);
            return false; //  false   
        }
    }

    // 8.      
    async GetUserStatusesForApartment(apartmentId, userId) {
        try {
            const response = await fetch(`${this.ApartmentAPIAddress}/api/Status/apartment/${apartmentId}/user/${userId}`, {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' }
            });

            if (!response.ok) {
                if (response.status === 404) {
                    console.log(` ${userId}     ${apartmentId}    .`);
                    return [];
                }
                const errorText = await response.text();
                throw new Error(` ${response.status}: ${errorText}`);
            }

            const data = await response.json();
            console.log(`  ${userId}   ${apartmentId}:`, data);
            return data;
        } catch (error) {
            console.error(`    ${userId}   ${apartmentId}:`, error.message || error);
            return []; //      
        }
    }
}